<style lang="postcss" scoped>
.home-container{

}
</style>
<template>
<section class="home-container">
    <MasterPage title="欢迎进入售楼销售系统">
        <div slot="title-icon">
            <Icon type="ios-grid" />
        </div>
         <div slot="paddingContent">
            <Row>
                <Col span="22" offset="1">
                    <Card dis-hover class="card_chart__">
                        <div id="myChart0" :style="{width: '80%', height: '600px'}"></div>
                    </Card>
                </Col>
            </Row>
        </div>
        
    </MasterPage>
</section>
</template>
<script>
import MasterPage from '@/components/Master'

export default {
    components:{
        MasterPage
    },
    data(){
      return{
        d: ''
      }
    },
    mounted () {
        this.charts4();
    },
    created () {
    },
    methods:{
      charts4() {
        let myChart4 = this.$echarts.init(document.getElementById('myChart0'))
        function getData(percent) {
          return [{
            value: percent,
            name: percent,
          }, {
            value: 1000 - percent,
            label: {
              show: false
            },
            labelLine: {
              normal: {
                show: false
              },
              emphasis: {
                show: false
              }
            },
            itemStyle: {
              normal: {
                color: 'transparent',
              }
            }
          }];
        }
        var colorL = ['#3eb4ff', '#009cff', '#0088ef', '#0074df', '#0057c8'];
        var colorR = ['#0ce4ff', '#00c7ea', '#00a9d1', '#0098c2'];

        var labelLine = {
          normal: {
            lineStyle: {
              color: '#cfd6d9',
              width: 2
            },
            smooth: 0.2,
            length: 50,
            length2: 50
          }
        }
        var label = {
          normal: {
            formatter: function(params) {
              if(params.color == "transparent")return;
              var splitParams = params.seriesName.split('-');
              return splitParams[1] + "( " + params.value+ " )" + splitParams[2];
            },
            textStyle: {
              color: '#39b0c1',
              fontSize:16
            }
          }
        }
        var giftImageUrl = "data:image/png;base64,";
        myChart4.setOption({
          title: {
              text: '销售财务总分析',
              subtext: '最近1年的数据',
              x:'center',
              textStyle: {
                  color: '#fff'
              }
          },
          backgroundColor: '#0E2A43',
                tooltip: {
                  trigger: 'item',
                  formatter: function(params, ticket, callback) {
                    if(params.color == "transparent")return;
                    var splitParams = params.seriesName.split('-');
                    return splitParams[1] + "( " + params.value+ ")" + splitParams[2];
                  }
                },
                graphic: {
                  elements: [{
                    type: 'image',
                    style: {
                      image: giftImageUrl,
                      width: 116,
                      height: 96
                    },
                    left: 'center',
                    top: 'center'
                  }]
                },
                series: [{
                    name: '余额-新增客户-人',
                    type: 'pie',
                    clockWise: false, //顺时加载
                    hoverAnimation: false, //鼠标移入变大
                    radius: [160, 175],
                    label: label,
                    labelLine: labelLine,
                    itemStyle: {
                      normal: {
                        color: colorL[0]
                      }
                    },
                    data: getData(181)
                  }, {
                    name: '余额-看房客户-人',
                    type: 'pie',
                    clockWise: false, //顺时加载
                    hoverAnimation: false, //鼠标移入变大
                    radius: [140, 155],
                    label: label,
                    labelLine: labelLine,
                    itemStyle: {
                      normal: {
                        color: colorL[1]
                      }
                    },
                    data: getData(876),
                  }, {
                    name: '余额-潜在客户-人',
                    type: 'pie',
                    clockWise: false, //顺时加载
                    hoverAnimation: false, //鼠标移入变大
                    radius: [120, 135],
                    label: label,
                    labelLine: labelLine,
                    itemStyle: {
                      normal: {
                        color: colorL[2]
                      }
                    },
                    data: getData(419)
                  }, {
                    name: '余额-同比增长-%',
                    type: 'pie',
                    clockWise: false, //顺时加载
                    hoverAnimation: false, //鼠标移入变大
                    radius: [100, 115],
                    label: label,
                    labelLine: labelLine,
                    itemStyle: {
                      normal: {
                        color: colorL[3]
                      }
                    },
                    data: getData(59)
                  },

                  {
                    name: '发放额-销售额-千万元',
                    type: 'pie',
                    clockWise: true, //顺时加载
                    hoverAnimation: false, //鼠标移入变大
                    radius: [160, 175],
                    label: label,
                    labelLine: labelLine,
                    itemStyle: {
                      normal: {
                        color: colorR[0]
                      }
                    },
                    data: getData(989)
                  }, {
                    name: '发放额-发放贷款-千万元',
                    type: 'pie',
                    clockWise: true, //顺时加载
                    hoverAnimation: false, //鼠标移入变大
                    radius: [140, 155],
                    label: label,
                    labelLine: labelLine,
                    itemStyle: {
                      normal: {
                        color: colorR[1]
                      }
                    },
                    data: getData(359)
                  }, {
                    name: '发放额-同比增长-%',
                    type: 'pie',
                    clockWise: true, //顺时加载
                    hoverAnimation: false, //鼠标移入变大
                    radius: [120, 135],
                    label: label,
                    labelLine: labelLine,
                    itemStyle: {
                      normal: {
                        color: colorR[2]
                      }
                    },
                    data: getData(89)
                  }
                ]
		          
        })
      
      }
    }
}
</script>
<style scoped>
.card_chart__{
  height: 600px;
}
</style>



