import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('@/components/Layout'),
      children:[
        {
          path: '',
          name: 'home',
          component: () => import('@/views/Home')
        },
        {
          path: '/home',
          name: 'test',
          component: () => import('@/views/Home')
        },
        {
          path: '/Houselist',
          name: 'test',
          component: () => import('@/views/house/houseList')
        },
        {
          path: '/repair',
          name: 'test',
          component: () => import('@/views/house/repair')
        },
        {
          path: '/purchased',
          name: 'test',
          component: () => import('@/views/Customer/purchased')
        },
        {
          path: '/purchasing',
          name: 'test',
          component: () => import('@/views/Customer/purchasing')
        },
        {
          path: '/achievement',
          name: 'test',
          component: () => import('@/views/Sale/achievement')
        },
        {
          path: '/sale',
          name: 'test',
          component: () => import('@/views/Sale/sale')
        },
        {
          path: '/charts',
          name: 'test',
          component: () => import('@/views/charts/charts')
        }
      ]
    },
    {
      path:'/login',
      name:'login',
      meta:{
        title:'登录',
      },
      component: () => import('@/views/Login')
    },
  ]
})
